.Media-logo {
    animation: Media-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    user-drag: none;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }

  @keyframes Media-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }